.TvNafa{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #4f4f4f;    
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
}
.screen-nafa {
  position: relative;
  width: 100%;
  height: 100%;
  animation: fadeIn-nafa 1s ease-out forwards; /* במקום 1.7s */
}


@keyframes fadeIn-nafa {
  0% {
    opacity: 0;
    transform: translateY(20px); /* מוסיף תחושת תנועה רכה */
  }




  100% {
    opacity: 1;
    transform: translateY(0);
  }
}


.Tv-img-nafa {
  position: absolute;
  width: 85vw;
  height: auto;
  top: 0vh;
  right: 50%;
  transform: translate(50%, 0);
z-index: 0;
}


.TvNafa .buttons-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px auto;
    position: absolute;
    width: 74vw;
    top: 80vh;
    left: 13vw;
    direction: rtl;
    gap: 33vw;
    z-index: 1000;
}


.TvNafa .btn-text {
    display: block;
}




.TvNafa .text-label {
    color: rgb(255, 255, 255);
}


.TvNafa .btn-text-next .img-arrow {
  background-image: url('/public/Assets/Btns/arrowWhite.png'); /* למשל תמונה אחרת */
}




.TvNafa .btn-text-prev .img-arrow {
  background-image: url('/public/Assets/Btns/arrowWhite.png');
}
.TvNafa .btn-text-end  .img-arrow {
  background-image: url('/public/Assets/Btns/arrowFullWhite.png'); /* תמונה אחרת */
}


