.background-part4 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.tomer-back {
    position: absolute;
    bottom: 0vh;
    width: 17vw;
    right: 50%;
    transform: translateX(50%);
}

.people-area {
    position: absolute;
    background-color: transparent;
    z-index: 30;
    cursor: pointer;
    width: 3.5vw;
    height: 15vh;
}

.relation-label {
    position: absolute;
    width: 170px;
    height: 40px;
    padding: 0;
    border-radius: 20px;
    font-weight: bold;
    background-color: transparent;
    font-size: 2vmin;
    /* z-index: 10; */
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    font-family: 'Assistant', sans-serif;
    cursor: default;
}

